import Logo from "./logo";

const canvas = document.getElementById("canvas");
const ctx = canvas.getContext('2d');
canvas.width = window.innerWidth;
canvas.height = window.innerHeight;
let imagePixelData = null;
const IMAGE_WIDTH = 100;
const IMAGE_HEIGHT = 35;
let MULTIPLIER = calculateMultipler();

let offsetX = ((canvas.width - IMAGE_WIDTH * MULTIPLIER) / 2) / MULTIPLIER;
let offsetY = ((canvas.height - IMAGE_HEIGHT * MULTIPLIER) / 2) / MULTIPLIER;

Logo.addEventListener("load", () => {
  ctx.drawImage(Logo,0,0,IMAGE_WIDTH,IMAGE_HEIGHT);
  imagePixelData = ctx.getImageData(0,0,IMAGE_WIDTH,IMAGE_HEIGHT);
  init();
  animate();
})

function calculateMultipler(){
  if (canvas.width < 550) return 3
  if (canvas.width < 750) return 5
  return 7
}

let particleArray = [];


const mouse = {
  x: null,
  y: null,
  radius: 75
}

window.addEventListener("mousemove", (event) => {
  mouse.x = event.x;
  mouse.y = event.y;
})

window.addEventListener("touchmove", (event) => {
  const x = event.touches[0].clientX;
  const y = event.touches[0].clientY;
  const r = event.touches[0].radiusX;
  mouse.x = x;
  mouse.y = y;
  mouse.radius = r;
})

class Particle {
  constructor(x, y){
    this.x = x;
    this.y = y;
    this.size = 1;
    this.baseX = x;
    this.baseY = y;
    this.density = Math.random() * 30 + 1
  }

  draw() {
    ctx.fillStyle = "yellow";
    ctx.beginPath();
    ctx.arc(this.x, this.y, this.size, 0, Math.PI*2, false);
    ctx.closePath();
    ctx.fill();
  }

  drawText() {
    ctx.fillText(`x:${Math.floor(this.x)}, y:${Math.floor(this.y)} `, this.x + this.size + 2, this.y - this.size + 2);
  }

  update() {
    const dx = mouse.x - this.x;
    const dy = mouse.y - this.y;
    const distance = Math.sqrt(dx * dx + dy * dy);
    const forceX = dx / distance;
    const forceY = dy / distance;
    const maxDistance = mouse.radius;
    const force = ( maxDistance - distance) / maxDistance;

    const directionX = forceX * force * this.density
    const directionY = forceY * force * this.density
    if (distance < maxDistance) {
      this.x -= directionX
      this.y -= directionY
    } else {
      if (this.x !== this.baseX) {
        let dx = this.x -this.baseX;
        this.x -= dx / 10;
      }
      if (this.y !== this.baseY) {
        let dy = this.y - this.baseY;
        this.y -= dy / 10;
      }
    }
  }
}

function init() {
  particleArray = [];
  const imageWidth = imagePixelData.width;
  const imageHeight = imagePixelData.height;

  for(let y = 0; y < imageHeight; y++) {
    for (let x = 0; x < imageWidth; x++) {
      // 4. value for alpha
      if (imagePixelData.data[(y * 4 * imagePixelData.width) + (x * 4) + 3] > 128) {
        let positionX = x + offsetX;
        let positionY = y + offsetY;
        particleArray.push(new Particle(positionX * MULTIPLIER, positionY * MULTIPLIER));
      }
    }
  }
}

let animationFrame = null
function animate() {
  ctx.clearRect(0,0,canvas.width, canvas.height);
  particleArray.forEach((particle,index) => {
    particle.draw();
    particle.update();
  });
  animationFrame = window.requestAnimationFrame(animate);
}

window.addEventListener("resize", () => {
  window.cancelAnimationFrame(animationFrame);
  // canvas.width = window.innerWidth;
  // canvas.height = window.innerHeight;
  MULTIPLIER = calculateMultipler();
  offsetX = ((canvas.width - IMAGE_WIDTH * MULTIPLIER) / 2) / MULTIPLIER;
  offsetY = ((canvas.height - IMAGE_HEIGHT * MULTIPLIER) / 2) / MULTIPLIER;
  init();
  animate();
})

